<template>
  <div id="user-info">
    
    <el-descriptions class="margin-top" title="个人信息" :column="3" :size="size" border v-loading="loading">
    <template slot="extra">
      <el-button icon="el-icon-edit" type="primary" size="small" v-show="edit" @click="edit = false">编辑</el-button>
      <el-button type="primary" plain size="small" v-show="!edit" @click="modifyInfo()">确定</el-button>
      <el-button size="small" plain v-show="!edit" @click="cancel">取消</el-button>
    </template>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-user"></i>
        姓名
      </template>
      <el-input v-show="!edit" v-model="tempInfo.name" size="small" clearable></el-input>
      <span v-show="edit">{{ this.tempInfo.name }}</span>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-mobile-phone"></i>
        联系方式
      </template>
      <el-input v-show="!edit" v-model="tempInfo.phone" size="small" clearable type="number"></el-input>
      <span v-show="edit">{{ tempInfo.phone }}</span>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-mobile-phone"></i>
        邮箱
      </template>
      <el-input v-show="!edit" v-model="tempInfo.username" size="small" clearable></el-input>
      <span v-show="edit">{{ tempInfo.username }}</span>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-location-outline"></i>
        居住地
      </template>
      <el-input style="padding: 0" v-show="!edit" v-model="tempInfo.address" size="small" clearable></el-input>
      <span v-show="edit">{{ tempInfo.address }}</span>
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-office-building"></i>
        部门
      </template>
      {{ tempInfo.departmentName }}
    </el-descriptions-item>
    <el-descriptions-item>
      <template slot="label">
        <i class="el-icon-tickets"></i>
        职务
      </template>
      <el-tag size="small">{{ tempInfo.positionName }}</el-tag>
    </el-descriptions-item>
  </el-descriptions>
  </div>
  
</template>

<script>
export default {
  name: 'userInfo',
  data() {
    return {
      size: '',
      edit: true,
      loading: false,
      tempInfo: {} 
    }
  },
  methods: {
    modifyInfo() {
      this.loading = true
      // 确认修改用户信息
      // 修改成功后弹出弹窗
      this.edit = true

      // 发送修改信息请求
      this.$request({
        url: 'users',
        method: 'PUT',
        data: JSON.stringify(this.tempInfo)
      }).then(res => {
        // 设置到全局
        this.$store.commit('setUserInfo', this.tempInfo)
        this.loading = false
        this.$message({
          message: '修改成功',
          type: 'success',
          duration: 2000
        })
      }, err => {
        this.$message({
          message: '修改失败',
          type: 'error',
          duration: 2000
        });
        this.$set(this.tempInfo, 'uid', info.uid)
        this.$set(this.tempInfo, 'name', info.name)
        this.$set(this.tempInfo, 'phone', info.phone)
        this.$set(this.tempInfo, 'address', info.address)
        this.$set(this.tempInfo, 'username', info.username)
      })
    },
    cancel() {
      this.edit = true
      this.tempInfo.name = this.$store.state.userInfo.name
      this.tempInfo.phone = this.$store.state.userInfo.phone
      this.tempInfo.address = this.$store.state.userInfo.address
      this.tempInfo.username = this.$store.state.userInfo.username
    }
  },
  created() {
    let uid = this.$store.state.userInfo.uid
    if (uid == null) {
      uid = JSON.parse(window.localStorage.getItem('userInfo').uid)
    }
    this.$request({
      url: '/users/' + this.$store.state.userInfo.uid
    }).then(res => {
      this.tempInfo = res.data

    })


  }
}
</script>

<style>
  #user-info {
    width: 1200px;
    margin: 100px auto 0 auto;
  }
</style>