<template>
  <div id="staff-management">
    <div id="title">员工数据列表</div>
    <el-drawer
      title="键入新信息!"
      :before-close="handleClose"
      :visible.sync="dialog"
      direction="rtl"
      custom-class="demo-drawer"
      ref="drawer"
      >
      <div class="demo-drawer__content">
        <el-form :model="form">
          <el-form-item label="姓名" :label-width="formLabelWidth">
            <el-input v-model="form.name" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="电话号" :label-width="formLabelWidth">
            <el-input v-model="form.phone" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="邮箱" :label-width="formLabelWidth">
            <el-input v-model="form.username" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="联系地址" :label-width="formLabelWidth">
            <el-input v-model="form.address" autocomplete="off"></el-input>
          </el-form-item>
          <el-form-item label="部门" :label-width="formLabelWidth">
            <el-select v-model="form.departmentId" placeholder="请选择部门">
              <el-option v-for="item in allDepartment" :label="item.departmentName" :value="item.departmentId" :key="item.departmentName"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="职务" :label-width="formLabelWidth">
            <el-select v-model="form.positionId" placeholder="请选择职务">
              <el-option v-for="item in allPosition" :label="item.positionName" :value="item.positionId" :key="item.positionName"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="账号类型" :label-width="formLabelWidth">
            <el-select v-model="form.role" placeholder="请选择账号类型">
              <el-option v-for="item in ['admin', 'normal']" :label="item" :value="item" :key="item"></el-option>
            </el-select>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button @click="cancelForm">取 消</el-button>
          <el-button type="primary" @click="$refs.drawer.closeDrawer()" :loading="loading">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        </div>
      </div>
    </el-drawer>
    
    <el-table
      :data="tableData.filter(data => !search || 
      data.username.toLowerCase().includes(search.toLowerCase()) || 
      data.name.toLowerCase().includes(search.toLowerCase())) ||
      data.address.toLowerCase().includes(search.toLowerCase())"
      style="width: 100%"
      height="500">
      <el-table-column
        label="姓名"
        width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.name }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="手机号"
        width="150">
        <template slot-scope="scope">
          <p>{{ scope.row.phone }}</p>
        </template>
      </el-table-column>
      <el-table-column
        label="邮箱">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.username }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="居住地">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.address }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="部门"
        width="100">
        <template slot-scope="scope">
          <span style="margin-left: 10px">{{ scope.row.departmentName }}</span>
        </template>
      </el-table-column>
      <el-table-column
        label="职务"
        width="100">
        <template slot-scope="scope">
          <el-tag size="medium">{{ scope.row.positionName }}</el-tag>
        </template>
      </el-table-column>
      <el-table-column
        label="账号类型"
        width="100">
        <template slot-scope="scope">
          <span>{{ scope.row.role }}</span>
        </template>
      </el-table-column>
      <el-table-column
      label="操作">
        <template slot-scope="scope">
          <el-button
            size="mini"
            @click="handleEdit(scope.$index, scope.row); dialog = true">编辑</el-button>
          <el-button
            size="mini"
            type="danger"
            @click="handleDelete(scope.$index, scope.row)">删除</el-button>
        </template>
      </el-table-column>
      
      <el-table-column
        align="right">
        <template slot="header" slot-scope="scope">
          <el-input
            v-model="search"
            size="mini"
            placeholder="输入关键字搜索"/>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: 'staffManagement',
  data() {
    return {
      tableData: [],
      dialog: false,
      loading: false,
      form: {
        uid: -1,
        name: '',
        phone: '',
        username: '',
        address: '',
        department: '',
        position: '',
      },
      formLabelWidth: '80px',
      timer: null,
      search: '',
      index: -1,
      allDepartment: {},
      allPosition: {}
    }
  },
  methods: {
    handleEdit(index, row) {
      this.form.uid = row.uid
      this.index = index
    },
    handleDelete(index, row) {
      this.$request({
        url: '/users/' + row.uid,
        method: 'DELETE'
      })
      this.tableData.splice(index, 1)
    },
    handleClose(done) {
      if (this.loading) {
        return;
      }
      this.$confirm('确定要提交表单吗？')
        .then(_ => {
          let data = {}
          for (let item in this.form) {
            if (this.form[item] != '' && this.form[item] != undefined) {
              data[item] = this.form[item]
            }
          }
          this.loading = true;
          this.$request({
            url: '/users',
            method: 'PUT',
            data: JSON.stringify(data)
          }).then(res => {
            
            for (let item in this.form) {
              if (this.form[item] != '' && this.form[item] != undefined) {
                this.tableData[this.index][item] = this.form[item]
              }
            }

            done();
            // 动画关闭需要一定的时间
            setTimeout(() => {
              this.loading = false;
            }, 400);
          })
        })
        .catch(_ => {});
    },
    cancelForm() {
      this.loading = false;
      this.dialog = false;
      this.form = {}
      clearTimeout(this.timer);
    }
  },
  created() {
    this.$request({
      url: '/users'
    }).then(res => {
      this.tableData = res.data
      
    })

    this.$request({
      url: '/departments'
    }).then(res => {
      this.allDepartment = res.data
    })

    this.$request({
      url: '/positions'
    }).then(res => {
      this.allPosition = res.data
    })
  }
}
</script>

<style>

#staff-management {
  width: 1300px;
  margin: 50px auto;
}
</style>