<template>
  <div id="home">
    <div id="left-bar">
      <div id="left-top">
        <img src="../assets/logo.png">
        <div>{{ '欢迎登录' + (this.$store.state.userInfo.name != null ? ', ' + this.$store.state.userInfo.name : '') }}!</div>
      </div>
      <el-menu
        style="border: none"
        default-active="1"
        class="el-menu-vertical-demo"
        @open="handleOpen"
        @close="handleClose"
        @select="select"
        background-color="#545c64"
        text-color="#fff"
        active-text-color="#ffd04b">
        <!-- <el-submenu index="1">
          <template slot="title">
            <i class="el-icon-location"></i>
            <span>导航一</span>
          </template>
          <el-menu-item-group>
            <template slot="title">分组一</template>
            <el-menu-item index="1-1">选项1</el-menu-item>
            <el-menu-item index="1-2">选项2</el-menu-item>
          </el-menu-item-group>
          <el-menu-item-group title="分组2">
            <el-menu-item index="1-3">选项3</el-menu-item>
          </el-menu-item-group>
          <el-submenu index="1-4">
            <template slot="title">选项4</template>
            <el-menu-item index="1-4-1">选项1</el-menu-item>
          </el-submenu>
        </el-submenu> -->
        <el-menu-item-group title="个人办公" v-if="this.$store.state.userInfo.role == 'normal' || this.$store.state.userInfo.role == null">
        <el-menu-item-group title="系统管理" v-if="this.$store.state.userInfo.role == 'admin'"></el-menu-item-group>
        </el-menu-item-group>
        <el-menu-item index="1" v-if="this.$store.state.userInfo.role == 'normal' || this.$store.state.userInfo.role == null">
            <i class="el-icon-user"></i>
            <span slot="title">个人信息</span>
        </el-menu-item>
        <el-menu-item index="2" v-if="this.$store.state.userInfo.role == 'normal' || this.$store.state.userInfo.role == null">
          <i class="el-icon-date"></i>
          <span slot="title">日程安排</span>
        </el-menu-item>
        <!-- <el-menu-item index="3">
          <i class="el-icon-document"></i>
          <span slot="title">名片夹</span>
        </el-menu-item> -->
        <el-menu-item index="4" v-if="this.$store.state.userInfo.role == 'normal' || this.$store.state.userInfo.role == null">
          <i class="el-icon-chat-line-square"></i>
          <span slot="title">公司通讯录</span>
        </el-menu-item>
        <el-menu-item index="8" v-if="this.$store.state.userInfo.role == 'admin'">
          <i class="el-icon-s-custom"></i>
          <span slot="title">员工管理</span>
        </el-menu-item>
        <el-menu-item index="9" v-if="this.$store.state.userInfo.role == 'normal'">
          <i class="el-icon-office-building"></i>
          <span slot="title">部门管理</span>
        </el-menu-item>
        <el-menu-item index="10" v-if="this.$store.state.userInfo.role == 'admin'">
          <i class="el-icon-office-building"></i>
          <span slot="title">职务管理</span>
        </el-menu-item>
        <el-menu-item index="5">
          <i class="el-icon-setting"></i>
          <span slot="title">修改密码</span>
        </el-menu-item>
        <el-menu-item index="6">
          <i class="el-icon-switch-button"></i>
          <span slot="title">退出登录</span>
        </el-menu-item>
      </el-menu>
    </div>
    <div id="content">
      <user-info v-show="index === '1'"></user-info>
      <schedule v-show="index === '2'"></schedule>
      <change-Password v-show="index === '5'"></change-Password>
      <addressBook :fa-msg="index" v-show="index === '4'"></addressBook>
      <staff-management v-show="index === '8'"></staff-management>
      <positionManagement v-show="index === '10'"></positionManagement>
      <departmentManagement v-show="index === '9'"></departmentManagement>
    </div>
  </div> 
</template>

<script>
import { setCookie, getCookie, delCookie } from '../network/cookie.js'
import userInfo from '@/components/userInfo.vue'
import changePassword from '@/components/changePassword.vue'
import schedule from '@/components/schedule.vue'
import staffManagement from '@/components/staffManagement.vue'
import addressBook from '@/components/addressBook.vue'
import meetingManagement from '@/components/meetingManagement.vue'
import positionManagement from '@/components/positionManagement.vue'
import departmentManagement from '@/components/departmentManagement.vue'
import { get } from 'http'

export default {
  data() {
    return {
      index: '1'
    }
  },
  methods: {
    handleOpen(key, keyPath) {
      console.log(key, keyPath);
    },
    handleClose(key, keyPath) {
      console.log(key, keyPath);
    },
    select(key) {
      if (key === '6') {
        delCookie('username')
        this.$router.replace('/')
      }
      this.index = key
    }
  },
  computed: {
    // active3() {
    //   if (this.$store.state.userInfo.role == 'normal' || this.$store.state.userInfo.role == null) {
    //     return 1
    //   }
    //   else {
    //     return 8
    //   }
    // },
  },
  components: {
    userInfo,
    changePassword,
    schedule,
    staffManagement,
    addressBook,
    meetingManagement,
    positionManagement,
    departmentManagement
  },
  beforeCreate() {
    
    this.$store.commit("setUserInfo", JSON.parse(window.localStorage.getItem('userInfo')))
  }
  ,
  created() {
    
    //获取cookie值
    let username = getCookie('username')
    if(username == '' || username =='undefined'){
      this.$router.replace('/login');
    }
    let that = this
    this.$request({
      url: '/api/user/info'
    }).then(res => {
      that.$store.commit("setUserInfo", res.data)
      if (res.data.role == 'admin') {
        that.index = '8'
      }
    })
  }
}
</script>

<style>
  #home {
    display: flex;
  }
  #left-bar {
    width: 16%;
    min-width: 140px;
    position: relative;
    height: 100vh;
    background-color: #545c64;
    box-shadow: 1px 0 0 rgba(0, 0, 0, 0.1);
  }

  #left-top {
    margin-bottom: 30px;
    color: white;
    width: 100%;
    /* display: flex; */
    text-align: center;
  }
  #left-top > img {
    width: 40%;
    border: 3px solid white;
    border-radius: 50%;
    margin: 30px 0;
  }

  #content {
    width: 100%;
  }
</style>