<template>
  <div id="schedule">
    <el-drawer
    title="键入新信息!"
    :before-close="handleClose"
    :visible.sync="dialog"
    direction="rtl"
    custom-class="demo-drawer"
    ref="drawer"
    >
      <div class="demo-drawer__content">
        <el-form :model="form">
          <el-form-item label="内容" :label-width="formLabelWidth">
            <el-input v-model="form.content" autocomplete="off"></el-input>
          </el-form-item>
          
          <el-form-item label="起止时间" :label-width="formLabelWidth">
            <el-time-picker
              is-range
              v-model="value1"
              range-separator="至"
              start-placeholder="开始时间"
              end-placeholder="结束时间"
              placeholder="选择时间范围">
            </el-time-picker>
          </el-form-item>
        </el-form>
        <div class="demo-drawer__footer">
          <el-button @click="cancelForm">取 消</el-button>
          <el-button type="primary" @click="$refs.drawer.closeDrawer()" :loading="loading">{{ loading ? '提交中 ...' : '确 定' }}</el-button>
        </div>
      </div>
    </el-drawer>

    <el-calendar id="calendar" v-model="value">
    </el-calendar>

    <div id="schedule-content">
      <el-button type="primary" style="margin: 10px;" @click="dialog = true">添加安排</el-button>
      <el-card class="box-card" v-for="(item, index) in allData[date]" :id="'_' + index" :key="item.content + index">
        <div slot="header" class="clearfix">
          <span>{{ item.begin.split(' ')[1] + ' - ' + item.end.split(' ')[1] }}</span>
          <el-button style="float: right; padding: 8px; margin-top: -6px;" type="danger" icon="el-icon-delete" circle @click="handelDelete(index)"></el-button>
        </div>
        <div class="text item">{{ item.content }}</div>
      </el-card>
      
    </div>

  </div>
</template>

<script>
import { text } from 'body-parser';

export default {
    name: "schedule",
    data() {
      return {
        value: new Date(),
        allData: {},
        dialog: false,
        loading: false,
        form: {
          uid: -1,
          content: '',
          begin: '',
          end: '',
        },
        formLabelWidth: '80px',
        timer: null,
        index: -1,
      };
    },
    methods: {
      handelDelete(index) {
        // this.$set(this.allData, this.date, this.allData[this.date].splice(index, 1))
        this.$request({
          url: '/schedules/' + this.allData[this.date][index].scheduleId,
          method: 'DELETE'
        }).then(res => {
          this.allData[this.date].splice(index, 1)
          document.getElementById('_' + index).remove()
          this.$message({
            message: '删除成功',
            type: 'success',
            duration: 2000
          })
        })
      },
      handleClose(done) {
        if (this.loading) {
          return;
        }
        let that = this
        this.$confirm('确定要提交表单吗？')
          .then(_ => {
            console.log(this.date)
            that.loading = true;
            that.$request({
              url: '/schedules',
              method: 'POST',
              data: {
                content: this.form.content,
                uid: this.$store.state.userInfo.uid,
                begin: `${this.date} ${this.value1[0].toLocaleTimeString()}`,
                end: `${this.date} ${this.value1[1].toLocaleTimeString()}`
              }
            }).then(res => {
              that.$message({
                message: '增加成功',
                type: 'success',
                duration: 2000
              })
              if (that.allData[that.date] == undefined) {
                that.allData[that.date] = []
              }
              that.allData[that.date].push(res.data)

              done();
              // 动画关闭需要一定的时间
              setTimeout(() => {
                that.loading = false;
              }, 400);
            })
          })
          .catch(_ => {});
      },
      cancelForm() {
        this.loading = false;
        this.dialog = false;
        this.form = {}
        clearTimeout(this.timer);
      }
    },
    computed: {
      date() {
        return `${this.value.getFullYear()}-${this.value.getMonth() + 1 < 10 ? ('' + this.value.getMonth() + 1) : this.value.getMonth() + 1}-${this.value.getDate() < 10 ? '0' + this.value.getDate() : this.value.getDate()}`
      },
      value1() {
        return [new Date(this.value.getFullYear(), this.value.getMonth(), this.value.getDate(), 8, 40), new Date(this.value.getFullYear(), this.value.getMonth(), this.value.getDate(), 9, 40)]
      }
    },
    created() {
      this.$request({
        url: '/users/' + this.$store.getters.getUserInfo.uid + '/schedules'
      }).then(res => {
        for (let item of res.data) {
          this.allData[item.begin.split(' ')[0]] = []
        }
        for (let item of res.data) {
          this.allData[item.begin.split(' ')[0]].push(item)
        }
      })

    }, 
    components: { text },
    
}
</script>

<style>
  #schedule {
    display: flex;
    justify-content: space-between;
  }
  #calendar {
    width: 800px;
  }
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
    word-break: break-all;
  }
  .el-card {
    box-shadow: none !important;
  }
  .box-card {
    margin-bottom: 10px
  }
  #schedule-content {
    overflow-y: auto;
    overflow-x: hidden;
    height:  100vh;
    width: 50%;
    padding-left: 10px;
  }
</style>