<template>
    <div id="department">
        <div id="title">部门数据列表</div>
        <div id="departmentData">
            <!-- slot是插槽，slot-scope="scope"相当于一行的数据 -->
            <!-- scope.row是该行的数据所有消息对象 -->
            <div id="search">
                <el-row>
                    <el-input
                        id="searchById"
                        placeholder="请输入部门名称"
                        prefix-icon="el-icon-search"
                        > 
                    </el-input>
                    <!-- <el-button id="searchBtn" type="info" icon="el-icon-search" @click="selectByName">搜索</el-button> -->
                </el-row>
            </div>
            <el-table
            :data="tableData"
            style="width: 100%"
                height="500">

            <el-table-column
                label="部门编号"
                width="180">
                <template slot-scope="scope">
                <span>{{ scope.row.departmentId }}</span>
                </template>
            </el-table-column>

            <el-table-column
                label="部门名称"
                width="180">
                <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                    <el-tag size="medium">{{ scope.row.departmentName }}</el-tag>
                    </div>
                </template>
            </el-table-column>

            <el-table-column
                label="部门描述"
                width="180">
                <template slot-scope="scope">
                <span>{{ scope.row.text }}</span>
                </template>
            </el-table-column>

            <el-table-column label="操作">
                <template slot-scope="scope">
                <el-button
                    size="mini"
                    @click="doEdit(scope)">编辑</el-button>
                <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope)">删除</el-button>

                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="编辑" width="50%" :visible.sync="isShow1">
            <el-form>
                <el-form-item label="部门名称">
                    <el-input v-model="formData.departmentName"></el-input>
                </el-form-item>
                <el-form-item label="部门描述">
                    <el-input v-model="formData.text"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" plain @click="submit();isShow=false">确认</el-button>
                </el-form-item>
        </el-form>
        </el-dialog>

        <el-dialog title="添加部门" width="50%" :visible.sync="isShow2">
            <el-form>
                <el-form-item label="部门名称">
                    <el-input v-model="formData.departmentName"></el-input>
                </el-form-item>
                <el-form-item label="部门描述">
                    <el-input v-model="formData.text"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" plain @click="submit2();isShow=false">确认</el-button>
                </el-form-item>
        </el-form>
        </el-dialog>
        </div>
        <el-button id="addNewDepartment" @click="doAdd" type="primary">
            <i class="el-icon-plus"></i>
            增加新部门
        </el-button>
    </div>
  </template>
  
  <script>
    export default {
        name:'departmentManagement',
        data() {
            return {
                index:0,
                isShow1:false,
                isShow2:false,
                input1:'',
                formData:{
                    departmentId:'',
                    departmentName:'',
                    text:'',
                },
                tableData: []
            }
        },
        methods: {
            doEdit(scope){
                this.index = scope.$index
                this.isShow1 = true
                this.formData = {...scope.row}
            },
            doAdd() {
                this.isShow2 = true
            },
            async submit(){
                this.$request({
                url: '/departments',
                method: 'PUT',
                data: {
                    departmentId: this.formData.departmentId,
                    departmentName: this.formData.departmentName,
                    text:this.formData.text
                }
            }).then(res => {
                this.formData = {}
                this.isShow1 = false
                if (res.code == 403) {  // 修改失败
                this.$message({
                    message: res.reason,
                    type: 'error',
                    duration: 2000
                })
                }
                else {
                //   setCookie('username', this.username, 1000*60)
                    this.tableData[this.index].departmentName = this.formData.departmentName
                    this.tableData[this.index].text = this.formData.text
                    this.$message({
                        message: '修改成功',
                        type: 'success',
                        duration: 2000
                    })
                }
            }, error => {
            })
        },
        // 添加部门
        async submit2(){
                this.$request({
                url: '/departments',
                method: 'POST',
                data: {
                    departmentName: this.formData.departmentName,
                    text: this.formData.text
                }
            }).then(res => {
                this.formData = {}
                this.isShow2 = false
                if (res.code == 403) {  // 添加失败
                this.$message({
                    message: res.reason,
                    type: 'error',
                    duration: 2000
                })
                }
                else {
                //   setCookie('username', this.username, 1000*60)
                    this.tableData.push(res.data)
                    this.$message({
                        message: '增加成功',
                        type: 'success',
                        duration: 2000
                    })
                }
            }, error => {
            })
        },
        handleDelete(scope){
            this.$request({
                url: "/departments/" + scope.row.departmentId + "/has-user"
            }).then(res => {
                if (!res.data) {
                    this.$request({
                        url: '/departments/' + scope.row.departmentId,
                        method: 'DELETE'
                    }).then(res => {
                        if (res.code == 403) {  // 删除失败
                        this.$message({
                            message: res.reason,
                            type: 'error',
                            duration: 2000
                        })
                        }
                        else {
                        //   setCookie('username', this.username, 1000*60)
                            this.tableData.splice(scope.$index, 1)
                            this.$message({
                                message: '删除成功',
                                type: 'success',
                                duration: 2000
                            })
                        }
                    }, error => {
                        console.log('请求失败稍后再试')
                        console.log(error)
                    })
                }
                else {
                    this.$message({
                        message: '部门不可删除，因为有员工在该部门下',
                        type: 'warning',
                        duration: 2000
                    })
                }
            })


        },
            // 查询该职务下是否有员工
        selectById(scope){
                this.$request({
                url: '/departments/{departmentId}/has-user',
                method: 'POST',
                data:{
                    departmentId:scope.row.departmentId
                }
            }).then(res => {
                if (res.code == 403) {  
                this.$message({
                    message: res.reason,
                    type: 'error',
                    duration: 2000
                })
                }
                else {
                }
            }, error => {
                console.log('请求失败稍后再试')
                console.log(error)
            })
            }
        },
        created() {
            this.$request({
                url: 'departments'
            }).then(res => {
                this.tableData = res.data
            })
        }
    }
  </script>

<style>
#department{
    width: 1300px;
    margin: 50px auto;
}
#title{
    width: 140px;
    height: 30px;
    font-size: 20px;
    font-weight: bolder;
    margin: 10px auto;
    color: #9c9c9c;
}
#search{
    position: relative;
    width: 300px;
}
#searchBtn{
    position: absolute;
    top:0px;
    right: 0px;
}
#searchById{
    width: 200px;
}

#departmentData{
    width: 80%;
    margin: 0 auto;
    position: relative;
}
#addNewDepartment{
    width: 200px;
    position: absolute;
    right: 300px;
    margin-top: 20px;
}
</style>