<template>
    <div id="addressBook">
      {{ changeData }}
        <el-table
          :data="tableData"
          style="width: 100%;font-size: 16px;text-align: center;"
          :default-sort = "{prop: 'name', order: 'descending'}"
          >
          <el-table-column
            prop="name"
            label="姓名"
            sortable
            width="100">
          </el-table-column>

          <el-table-column
            prop="departmentName"
            label="部门"
            width="100">
          </el-table-column>

          <el-table-column
            prop="positionName"
            label="职位"
            width="180">
          </el-table-column>

          <el-table-column
            prop="phone"
            label="电话"
            width="150">
          </el-table-column>

          <el-table-column
            prop="username"
            label="邮箱"
            width="250">
          </el-table-column>
          <el-table-column
            prop="address"
            label="地址"
            >
          </el-table-column>
        </el-table>
        <div id="pages">
          <el-pagination
            background
            layout="prev, pager, next"
            :total="pagination.total"
            :page-size="pagination.pageSize"
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page.sync="pagination.pageNum"
            >
        </el-pagination>
        </div>
    </div>
</template>
  
<script>
    export default {
      data() {
        return {
          tableData: [],
          pagination: {
            pageNum: 1,
            total: 10,//接口获取
            pageSize: 10//每页显示多少条数据
          }
        }
      },
      props:['faMsg'],
      computed: {
        changeData() {

          this.getData()
          console.log(this.faMsg)

          return this.faMsg
        }
      },
      mounted(){
        this.getData();
      },
      methods: {
        formatter(row, column) {
          return row.address;
        },
        getData(){
          
          let DataAll = []

          this.$request({
            url: '/users'
          }).then(res => {
            console.log(res)
            DataAll = res.data
            this.tableData=[]
            //第一步：当前页的第一条数据在总数据中的位置
            let strlength = (this.pagination.pageNum - 1) * this.pagination.pageSize + 1;
            //第二步：当前页的最后一条数据在总数据中的位置
            let endlength = this.pagination.pageNum * this.pagination.pageSize;
            //第三步：当分页的页数在最后一页时，进行重新筛选获取数据时
            //获取本次表格最后一页第一条数据所在的位置的长度
            let resStrLength=0
            if(DataAll.length % this.pagination.pageSize == 0){
              resStrLength = (parseInt(DataAll.length / this.pagination.pageSize)-1) * this.pagination.pageSize + 1
            }else{
              resStrLength = parseInt(DataAll.length / this.pagination.pageSize) * this.pagination.pageSize + 1
            }
            //如果上一次表格的最后一页第一条数据所在的位置的长度 大于 本次表格最后一页第一条数据所在的位置的长度，则将本次表格的最后一页第一条数据所在的位置的长度 为最后长度
            if(strlength>resStrLength){
              strlength=resStrLength
            }
            //第四步：当分页的页数切换至最后一页，如果最后一页获取到的数据长度不足最后一页设置的长度，则将设置的长度 以 获取到的数据长度 为最后长度
            if (endlength > DataAll.length) {
              endlength = DataAll.length;
            }
            //第五步：循环获取当前页数的数据，放入展示的数组中
            for (let i = strlength - 1; i < endlength; i++) {
              this.tableData.push(DataAll[i]);
            }
            //数据的总条数
            this.pagination.total = DataAll.length;

          })

        },
        //切换当前页显示的数据条数，执行方法
        handleSizeChange(val) {
          this.pagination.pageSize=val
          this.getData()
        },
        //切换页数，执行方法
        handleCurrentChange(val) {
          this.pagination.pageNum=val
          this.getData()
        }
      },
      }
    
</script>

<style>
  #addressBook{
    width:90%;
    height: 700px;
    margin: 50px auto;
  }
  #pages{
    width: 400px;
    height: 30px;
    margin: 20px auto;
  }
</style>