<template>
    <div id="position">
        <div id="title">职务数据列表</div>
        <div id="positionData">
            <div id="search">
                <el-row>
                    <el-input
                        id="searchById"
                        placeholder="请输入职务名称"
                        prefix-icon="el-icon-search"
                        > 
                    </el-input>
                    <!-- <el-button id="searchBtn" type="info" icon="el-icon-search" @click="selectByName">搜索</el-button> -->
                </el-row>
            </div>
            <!-- slot是插槽，slot-scope="scope"相当于一行的数据 -->
            <!-- scope.row是该行的数据所有消息对象 -->
            <el-table
            :data="tableData"
            style="width: 100%"
            height="500">

            <el-table-column
                label="职务编号"
                width="180">
                <template slot-scope="scope">
                <span>{{ scope.row.positionId }}</span>
                </template>
            </el-table-column>

            <el-table-column
                label="职务名称"
                width="180">
                <template slot-scope="scope">
                <div slot="reference" class="name-wrapper">
                    <el-tag size="medium">{{ scope.row.positionName }}</el-tag>
                    </div>
                </template>
            </el-table-column>

            <el-table-column
                label="最低工资"
                width="180">
                <template slot-scope="scope">
                <span>{{ scope.row.minWage }}</span>
                </template>
            </el-table-column>

            <el-table-column
                label="最高工资"
                width="180">
                <template slot-scope="scope">
                <span>{{ scope.row.maxWage }}</span>
                </template>
            </el-table-column>

            <el-table-column label="操作">
                <template slot-scope="scope">
                <el-button
                    size="mini"
                    @click="doEdit(scope)">编辑</el-button>
                <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope)">删除</el-button>

                </template>
            </el-table-column>
        </el-table>

        <el-dialog title="编辑" width="50%" :visible.sync="isShow1">
            <el-form>
                <el-form-item label="职务名称">
                    <el-input v-model="formData.positionName"></el-input>
                </el-form-item>
                <el-form-item label="最低工资">
                    <el-input v-model="formData.minWage"></el-input>
                </el-form-item>
                <el-form-item label="最高工资">
                    <el-input v-model="formData.maxWage"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" plain @click="submit();isShow1=false">确认</el-button>
                </el-form-item>
        </el-form>
        </el-dialog>

        <el-dialog title="添加职务" width="50%" :visible.sync="isShow2">
            <el-form>
                <el-form-item label="职务名称">
                    <el-input v-model="formData.positionName"></el-input>
                </el-form-item>
                <el-form-item label="最低工资">
                    <el-input v-model="formData.minWage"></el-input>
                </el-form-item>
                <el-form-item label="最高工资">
                    <el-input v-model="formData.maxWage"></el-input>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" plain @click="submit2();isShow2=false">确认</el-button>
                </el-form-item>
        </el-form>
        </el-dialog>
        </div>
        <el-button type="primary" id="addNewPosition" @click="doAdd">
            <i class="el-icon-plus"></i>
            增加新职务
        </el-button>
    </div>
  </template>
  
  <script>
    export default {
        name:'positionManagement',
        data() {
            return {
                index:0,
                isShow1:false,
                isShow2:false,
                formData:{
                    positionId:'',
                    positionName:'',
                    minWage:'',
                    maxWage:''
                },
                tableData: []
            }
        },
        methods: {
            doEdit(scope){
                this.index = scope.$index
                this.isShow1 = true
                this.formData = {...scope.row}
            },
            doAdd() {
                this.isShow2 = true
            },
            async submit(){
                this.$request({
                url: '/positions',
                method: 'PUT',
                data: {
                    positionId: this.formData.positionId,
                    positionName: this.formData.positionName,
                    maxWage: this.formData.maxWage,
                    minWage: this.formData.minWage,
                }
            }).then(res => {
            this.formData = {}
                this.isShow1 = false
                if (res.code == 403) {  // 修改失败
                this.$message({
                    message: res.reason,
                    type: 'error',
                    duration: 2000
                })
                }
                else {
                //   setCookie('username', this.username, 1000*60)
                    this.tableData[this.index].positionName = this.formData.positionName
                    this.tableData[this.index].maxWage = this.formData.maxWage
                    this.tableData[this.index].minWage = this.formData.minWage
                    this.$message({
                        message: '修改成功',
                        type: 'success',
                        duration: 2000
                    })
                }
            }, error => {
                console.log('请求失败稍后再试')
                console.log(error)
            })
        },
        // 添加职务
        async submit2(){
            this.$request({
                url: '/positions',
                method: 'POST',
                data: {
                    positionName: this.formData.positionName,
                    minWage: this.formData.minWage,
                    maxWage: this.formData.maxWage
                }
            }).then(res => {
            this.formData = {}
                this.isShow2 = false
                if (res.code == 403) {  // 添加失败
                this.$message({
                    message: res.reason,
                    type: 'error',
                    duration: 2000
                })
                }
                else {
                //   setCookie('username', this.username, 1000*60)
                    this.tableData.push(res.data)
                    this.$message({
                        message: '增加成功',
                        type: 'success',
                        duration: 2000
                    })
                }
            }, error => {
                console.log('请求失败稍后再试')
                console.log(error)
            })
        },
        handleDelete(scope){
            this.$request({
                url: "/positions/" + scope.row.positionId + "/has-user"
            }).then(res => {
                if (!res.data) {
                    this.$request({
                        url: '/positions/' + scope.row.positionId,
                        method: 'DELETE'
                    }).then(res => {
                        if (res.code == 403) {  // 删除失败
                        this.$message({
                            message: res.reason,
                            type: 'error',
                            duration: 2000
                        })
                        }
                        else {
                        //   setCookie('username', this.username, 1000*60)
                            this.tableData.splice(scope.$index, 1)
                            this.$message({
                                message: '删除成功',
                                type: 'success',
                                duration: 2000
                            })
                        }
                    }, error => {
                        console.log('请求失败稍后再试')
                        console.log(error)
                    })
                }
                else {
                    this.$message({
                        message: '职务不可删除，因为有员工在该职务下',
                        type: 'warning',
                        duration: 2000
                    })
                }
            })
        },
            // 查询该职务下是否有员工
        selectById(scope){
            this.$request({
            url: '/positions/{positionId}/has-user',
            method: 'POST',
            data:{
                positionId:scope.row.positionId
            }
            }).then(res => {
                if (res.code == 403) {  
                this.$message({
                    message: res.reason,
                    type: 'error',
                    duration: 2000
                })
                }
                else {
                //   setCookie('username', this.username, 1000*60)
                this.$router.replace('/positionManagement')
                }
            }, error => {
                console.log('请求失败稍后再试')
                console.log(error)
            })
            }
        },
        created() {
            this.$request({
                url: 'positions'
            }).then(res => {
                this.tableData = res.data
            })
        }
    }
  </script>

<style>
#position{
    width: 1300px;
    margin: 50px auto;
}
#title{
    width: 140px;
    height: 30px;
    font-size: 20px;
    font-weight: bolder;
    margin: 10px auto;
    color: #9c9c9c;
}
#search{
    position: relative;
    width: 300px;
}
#searchBtn{
    position: absolute;
    top:0px;
    right: 0px;
}
#searchById{
    width: 200px;
}
#positionData{
    width: 80%;
    margin: 0 auto;
    position: relative;
}
#addNewPosition{
    width: 200px;
    position: absolute;
    right: 300px;
    margin-top: 20px;
    color:#fff;
}
</style>