<template>
  <div id="resetPassword">
    <!-- <el-dialog title="重置密码" :visible.sync="ResetPwdDialogVisible" width="40%" @close="resetForm('pwdForm')"> -->
  <el-form :model="resetPwdForm" status-icon :rules="resetPwdRules" ref="pwdForm" label-width="100px">
    <el-form-item label="密码" prop="oldPassword">
      <el-input prefix-icon="iconfont icon-showpassword" placeholder="请输入当前密码" type="password" v-model="resetPwdForm.oldPassword" autocomplete="off">
       </el-input>
    </el-form-item>
    <el-form-item label="新密码" prop="newPassword">
      <el-input prefix-icon="iconfont icon-showpassword" placeholder="请输入新密码" 
        type="password" v-model="resetPwdForm.newPassword" autocomplete="off">
      </el-input>
    </el-form-item>
    <el-form-item label="确认密码" prop="password_again">
       <el-input prefix-icon="iconfont icon-showpassword" placeholder="请再次输入新密码" 
        type="password" v-model="resetPwdForm.password_again" autocomplete="off">
      </el-input>
    </el-form-item>
    <el-form-item>
       <el-button type="primary" @click="submitForm('pwdForm')">确认修改</el-button>
        <el-button type="info" @click="resetForm('pwdForm')">重置</el-button>
    </el-form-item>
  </el-form>
 <!-- </el-dialog> -->
  </div>
</template>

<script>
  export default {
    name:'changePassword',
    data(){
        var validatePass = (rule, value, callback) => {
          if (value === '') {
              callback(new Error('请输入密码'));
          } else {
              if (this.resetPwdForm.password_again !== '') {
                  this.$refs.pwdForm.validateField('password_again');
              }
              callback();
          }
      };
          var validatePass2 = (rule, value, callback) => {
          if (value === '') {
              callback(new Error('请再次输入密码'));
          } else if (value !== this.resetPwdForm.newPassword) {
              callback(new Error('两次输入密码不一致!'));
          } else {
              callback();
          }
          };
        return{
            ResetPwdDialogVisible: false,
              resetPwdForm: {
                  uid: '',
                  oldPassword: '',
                  newPassword: '',
                  password_again: '',
              },
        resetPwdRules: {
            oldPassword: [
                { required: true, message: '请输入密码', trigger: 'blur' },
                {
                    min: 6,
                    max: 18,
                    message: '用户密码的长度在6~18个字符',
                    trigger: 'blur'
                },
                { validator: validatePass, trigger: 'blur' }
            ],
            newPassword: [
                { required: true, message: '请输入新密码', trigger: 'blur' },
                {
                    min: 6,
                    max: 18,
                    message: '用户密码的长度在6~18个字符',
                    trigger: 'blur'
                },
                { validator: validatePass, trigger: 'blur' }
            ],
            password_again: [
                { required: true, message: '请确认新密码', trigger: 'blur' },
                {
                    min: 6,
                    max: 18,
                    message: '用户密码的长度在6~18个字符',
                    trigger: 'blur'
                },
                { validator: validatePass2, trigger: 'blur' }
            ],
        }
      }
    },
      methods: {
          submitForm(formName) {
          this.resetPwdForm.uid =  this.$store.state.userInfo.uid;
          this.$refs[formName].validate(async valid => {
          if (!valid) return;
            this.$request({
              url: '/users/new',
              method: 'PUT',
              data: this.resetPwdForm
            }).then(res => {
              if (res.code == 200) {
                this.$message.success("重置密码成功");
                this.resetPwdForm = {}
              }
              else {
                this.$message.error("原密码错误");
              }
            })
          });
          },
          resetForm(formName) { //重置
            this.$refs[formName].resetFields();
          }
    }
  }
</script>

<style>
  #resetPassword{
    width: 400px;
    height: 200px;
    margin: 50px auto;
    padding-right: 100px;
  }
</style>